html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

button {
  border: none;
  padding: 0;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}

h1 {
  font-size: 2.625em;
  font-weight: 700;
  margin-bottom: 24px;
}

p {
  line-height: 1.5em;
}

.container {
  max-width: 1440px;
  margin: auto;
}

.primaryButton {
  background-color: #e4c1ae;
  padding: 16px 40px;
  font-size: 1em;
  font-weight: 700;
  color: #fff;
  border-radius: 32px;
  margin-top: 32px;
}

.heroSection {
  display: flex;
}
.heroSection .heroSectionImage {
  width: 50%;
  overflow: hidden;
}
.heroSection .heroSectionText {
  width: 50%;
  padding: 0 95px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.heroSection .heroSectionText button {
  align-self: flex-start;
}

@media (max-width: 992px) {
  .heroSectionText {
    padding: 50px 50px !important;
  }
}
@media (max-width: 768px) {
  .heroSection {
    flex-direction: column;
  }
  .heroSection .heroSectionImage,
.heroSection .heroSectionText {
    width: 100%;
  }
  .heroSection .heroSectionText h1 {
    font-size: 2.25em;
  }
}